
.address-headbar-bg{
  background: #d81e06;
}
.edit{
  font-size: .16rem;
  position: absolute;
  top: 0.14rem;
  right: 0.1rem;
  z-index: 100;
  color: white;
  font-weight: 400;
}
.address-page{
  padding-top: .44rem;
  z-index: 202;
}
.address-content{
  background: #f1f1f1;
  overflow: scroll;
}
.address-content>ul{
  margin-top: .13rem;
  border-top: 1px solid #ddd;
  background: white;
  color: #777;
}
.address-content>ul>li{
  padding: .14rem .1rem;
  font-size: .15rem;
  border-bottom: 1px solid #ddd;
}
.address-content>ul>li input{
  outline: none;
  border: none;
  padding: 0;
  font-size: .15rem;
  width: 3.2rem;
}
.address-content>ul>li img{
  width: .1rem;
  vertical-align: middle;
  padding-left: .03rem;
}
.address-content>ul>li ul{
  display: inline-block;
  padding-left: .1rem;
  float: right;
}
.address-content>ul>li ul li{
  display: inline-block;
  padding: 0 .05rem;
}
.address-content>ul>li:last-child input{
  float: right;
  width: .15rem;
  height: .15rem;
  border: 1px solid #ddd;
  outline: none;
}
.address-content>ul>li:last-child input:checked{
  border: none;
}
.address-content .sure_btn{
  position: absolute;
  left: .29rem;
  bottom: .37rem;
  background: #f29004;
  width: 3.2rem;
  height: .46rem;
  color: white;
  font-size: .17rem;
  border-radius: 5px;
  border: none;
  outline: none;
}
.city-mask{
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.3);
  position: fixed;
  top: 0px;
  z-index: 205;
}
.city-mask>div{
  width: 100%;
  /*height: 2.62rem;*/
  background: white;
  position: fixed;
  bottom: 0;
}
.city-mask>div>p{
  height: .47rem;
  background: #ddd;
}
.city-mask>div>p button{
  height: 100%;
  font-size: .15rem;
  color: #333;
}
.city-mask>div>p button:first-child{
  float: left;
}
.city-mask>div>p button:last-child{
  float: right;
}
.city-List{
  padding: 0 .09rem;
  height: 2.13rem;
  position: relative;
}
.city-List span{
  position: absolute;
  display: block;
  width: 3.45rem;
  border-top: 1px solid #ddd; 
  border-bottom: 1px solid #ddd;
  height: .37rem;
  top: .88rem; 
  z-index: -1;
}
.city-List ul{
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  overflow: scroll;
}
.city-List div{
  display: inline-block;
  height: 2.14rem;
  line-height: 2.14rem;
  padding-right: .21rem;
  color: #f29004;
  vertical-align: middle;
}
.city-List div:last-child{
  padding-right: 0;
}
.city-List ul li{
  padding: .1339rem .21rem;
}



