
.fullpage{
  min-height: 100%;
  background: white;
}
.shoppingCar-headbar-bg {
  background: #d81e06;
}
.shoppingCar-content{
  font-size: .16rem;
  background: white;
}
input[type="checkbox"]{
  width: .15rem;
  height: .15rem;
  border: none;
  outline: none;
}
.shoppingCar-content>ul{
  margin-bottom: .59rem;
}
.edit{
  font-size: .16rem;
  position: absolute;
  top: 0.14rem;
  right: 0.1rem;
  z-index: 100;
  color: white;
  font-weight: 400;
}
.pay-bar{
  position: fixed;
  bottom: .49rem;
  width: 100%;
  height: .58rem;
  background: #f1f1f1;
  line-height: .58rem;
}
.pay-bar div{
  display: inline-block;
  vertical-align: middle;
}
.pay-bar>div:nth-child(1){
  padding-left: 0.1rem;
  color: #343434;
  font-size: .17rem;
  height: .58rem;
}
.pay-bar>div:nth-child(1) input{
  margin-right: .08rem;
}
.pay-bar>div:nth-child(2) div{
  margin: .12rem 0 0 .22rem;
  color: #343434;
  line-height: .2rem;
}
.pay-bar>div:nth-child(2) div{
  margin: .12rem 0 .06rem .22rem;
  color: #343434;
}
.pay-bar>div:nth-child(2) div span{
  color: #ed601b;
}
.pay-bar>div:nth-child(2) div:after{
  content: "()";
  font-size: .13rem;
  color: #999;
  display: block;
}
.pay-bar button{
  position: absolute;
  top: 0.11rem;
  right: 0.1rem;
  background: #ee4147;
  color: white;
  font-size: .17rem;
  width: 1.125rem;
  height: .36rem;
  outline: none;
  border: none;
  border-radius: 5px;
}
.carItem{
  padding: .14rem .1rem;
  border-bottom: 1px solid #e1e1e1;
  position: relative;
}
input[type="checkbox"]{
  border-radius: 100%;
  border: 1px solid #ddd;
  -webkit-appearance: none;
  vertical-align: middle;
}
input[type="checkbox"]:checked{
  border: none;
  background: url(images/selected.png) no-repeat;
  background-size: 100% 100%;
}
.carItem img{
  width: .69rem;
  height: .68.5rem;
  vertical-align: middle;
}
.carItem{
  vertical-align: middle;
}
.carItem>div{
  display: inline-block;
}
.carItem>div>div{
  display: inline-block;
  width: 2.4rem;
  vertical-align: middle;
}
.carItem>div>div p:nth-child(1){
  width: 2rem;
  color: #1a1a1a;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.carItem>div>div p:nth-child(2){
  color: #757575;
  font-size: .13rem;
  padding: .11rem 0;
}
.carItem>div>div p:nth-child(3)>span{
  color: #de0000;
  font-size: .13rem;
  font-weight: bolder;
}
.carItem>div>span{
  position: absolute;
  right: .1rem;
  top: .68rem;
}
.carItem>div .edit-amount{
  position: absolute;
  right: .1rem;
  top: .6rem !important;
}
.carItem>div .edit-amount>span:first-child{
  display: inline-block;
  width: .2rem;
  height: .2rem;
  font-size: .2rem;
  line-height: .2rem;
  text-align: center;
  border-radius: 100%;
  border: 1px solid #e1e1e1;
}
.carItem>div .edit-amount>span:last-child{
  display: inline-block;
  width: .2rem;
  height: .2rem;
  font-size: .2rem;
  color: #f29004;
  line-height: .2rem;
  text-align: center;
  border-radius: 100%;
  border: 1px solid #e1e1e1;
}
.canReduce{
  color: #f29004;
}
.add_del-enter-active {
  transition: all .3s ease;
}
.add_del-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.add_del-leave-to,.add_del-enter,.add_del-leave{
  transform: translateX(100%);
}



