
.goods-list-item{
  position: relative;
  padding: .1rem 0;
  overflow: hidden;
}
.goods-list-item span{
  position: absolute;
  right: 0;
  bottom: .1rem;
}
.goods-list-item img{
  margin-right: .1rem;
  float: left;
  width: .8rem;
  height: .8rem;
}
.goods-list-item p{
  line-height: .27rem;
}
.goods-list-item p:nth-child(3){
  color: #828383;
}
.goods-list-item p:nth-child(4){
  color: #CB0003;
}



